$green-dark: #06524d;
$green-light: #2b9b77;
.button{
  &_gradient{
    .button__gradient{
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      background: linear-gradient( 90deg, $green-light 0%, $green-dark 100%);
    }
    .button__text{
      background: transparent;
      -webkit-background-clip: unset;
      -webkit-text-fill-color: #fff;
    }
    &:hover{
      .button__text{
        background: transparent;
        -webkit-background-clip: unset;
        -webkit-text-fill-color: #fff;
      }
    }
  }
  &_white{
    background: linear-gradient( -90deg, $green-light 0%, $green-dark 100%);
    .button__gradient{
      display: block;
    }
    .button__text{
      display: inline-block;
      background: transparent;
      -webkit-background-clip: unset;
      -webkit-text-fill-color: #fff;
    }
  }
}
